import React from 'react';
import { Layout, Wrapper, CardHeader } from '../components';
import Helmet from 'react-helmet';
import { Card, CardContent } from '@material-ui/core';

const HTMLTitle = '关于站点';

interface States {
  title: string;
}

export default class AboutPage extends React.Component<{}, States> {
  constructor(props: any) {
    super(props);

    this.state = {
      title: HTMLTitle,
    };
  }
  public render() {
    const { title } = this.state;
    return (
      <Layout>
        <Helmet title={title} />
        <Wrapper>
          <Card>
            <CardHeader>
              <h2>{title}</h2>
            </CardHeader>
            <CardContent>
              <h3>关注</h3>
              <h3>帮助</h3>
              <h4 id="qa">常见问题</h4>
              <p>暂无</p>
              <h4 id="tutorial">使用教程</h4>
              <p>暂无</p>
            </CardContent>
          </Card>
        </Wrapper>
      </Layout>
    );
  }
}
